*
{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.content
{
  height: 100vh;
}
.navbar
{
  background-color: rgb(175, 174, 174) !important;
}
.resgister-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
/* transaction page */
.filters
{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0  3px gray;
  flex-wrap: wrap;
}

.table-container {
  overflow-x: auto;
  font-size: 14px; /* Adjust the font size as needed */
}

@media (max-width: 768px) {
  .table-container {
    overflow-x: auto;
    font-size: 1rem; /* Adjust the font size for small screens */
  }
}
/* ant icons */
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon
{
  color: black;
}
.inactive-icon
{
  color: gray;
}
.switch-icon
{
  border: 1px solid rgb(10, 10, 10);
  border-radius:5px;
  padding: 10px 15px;
}
.foot
{
  display: flex;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  justify-content: center;
  opacity: 0.7;
}
/* expanse ke liye  */
.mainbox
{
  display: flex;
  flex-direction: column;
  flex-wrap:wrap;
}

